import React, {Component} from "react";
import {connect} from "react-redux";

import DataLayerUtils from "eshop/utils/DataLayerUtils";
import {registerCurrentStepId} from "../../actions/app";
import OraFullPageLoaderComponent from "../../../fix/components/OraFullPageLoaderComponent";
import {isFullPageLoader} from "../../selectors";

class MulticartCheckoutProgressComponent extends Component {

    constructor(props) {
        super(props);
    }

    componentWillMount() {
        this.props.registerCurrentStepId(this.props.progressBarId);
    }

    getStepView(step, currentStepNumber) {
        let stepDescriptionId = "checkoutStepTitle" + step.stepNumber;
        let description = this.props.descriptions[stepDescriptionId];

        return (
            <li key={stepDescriptionId} className={"o-steps__li" + this.getStepItemClass(currentStepNumber, step.stepNumber)}>
                <div className="o-steps__item">
                    <span className="o-steps__content">{description}</span>
                </div>
            </li>
        );
    }

    getStepItemClass(currentStepNumber, stepNumber) {
        if(currentStepNumber === stepNumber) {
            return " o-steps__li--active";
        } else if(currentStepNumber === stepNumber - 1) {
            return " o-steps__li--prev-active";
        } else {
            return "";
        }
    }

    render() {
        let currentStep = this.props.checkoutSteps.find(step => step.progressBarId === this.props.progressBarId);
        let selected = currentStep ? currentStep.stepNumber : 1;
        return (
            <div data-js-module="common/modules/opl-steps" className={"o-steps opl-steps u-padding-top-l u-small-no-padding-t "+(selected==3?"u-padding-l-xl":"u-padding-l")}>
                <ul className="o-steps__list">
                    {this.props.checkoutSteps.map((step, index, options) => {
                        return this.getStepView(step, selected);
                    })}
                </ul>
                <OraFullPageLoaderComponent isShow={this.props.showFullPageLoader}/>
            </div>
        )
    }
}

const mapStateToProps = state => ({
    showFullPageLoader: isFullPageLoader(state)
});

const mapDispatchToProps = (dispatch) => ({
    registerCurrentStepId: (value) => dispatch(registerCurrentStepId(value))
});

export default MulticartCheckoutProgressComponent = connect(mapStateToProps, mapDispatchToProps)(MulticartCheckoutProgressComponent);
